@charset "utf-8";
@import "../includes/zindex";

body.app-layout {
  .prod-detail-main .prod-detail-slide {
    top: 0;
  }
}
.bnr-more {
  width: 7.733vw;
  height: 7.733vw;
  display: block;
  background: url('/_ui/img/m/icn/icn-more.png') no-repeat;
  background-size: 100%;
  font-size: 0;
  text-indent: -999px;
  margin: 6.666vw auto;
}
.prod-detail-main {
  position: relative;
  padding-top: 122.544vw;
  .prod-detail-slide {
    position: fixed;
    top: 14.666vw;
    left: 0;
    width: 100%;
    .swiper-slide img {
      width: 81.333vw;
      height: 122vw;
      margin: 0 auto;
    }
  }
  .detail-info {
    position: relative;
    background: #fff;
    .info-box {
      position: relative;
      height: 34.133vw;
      border-bottom: 1px #f1f1f1 solid;
      text-align: center;
      color: #1f1f1f;
      .info-wrap {
        position: absolute;
        left: 0;
        right: 0;
        top: 50%;
        padding: 0 6.133vw;
        -webkit-transform: translateY(-50%);
        -moz-transform: translateY(-50%);
        -o-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
        transform: translateY(-50%);
        text-align: left;
      }
      .brand {
        float: left;
        font-size: 2.933vw;
        font-weight: 900;
        font-family: 'helvetica';
        i {
          display: inline-block;
          margin-left: 1.066vw;
          padding: .266vw 1.2vw;
          font-family: 'Noto Sans KR', sans-serif;
          font-size: 2.933vw;
          color: #fff;
          background: #aaa;
        }
      }
      .code {
        float: right;
        font-size: 2.666vw;
        color: #aaa;
      }
      .prod {
        clear: both;
        margin: 5.33vw auto 4.8vw;
        font-size: 4.133vw;
      }
      .price-info {
        font-size: 0;
        letter-spacing: 0;
        word-spacing: 0;
        .discount {
          color: #9ba4b0;
          font-weight: 300;
          font-size: 3.2vw;
          margin-right: 2.4vw;
          i {
            text-decoration: line-through;
          }
        }
        .percent {
          font-weight: 700;
          font-size: 3.2vw;
          margin-left: 1.6vw;
          i {
            font-weight: 700;
            &::before {
              content: '';
              display: inline-block;
              width: 1.866vw;
              height: 3.066vw;
              background: url('/_ui/img/m/icn/icn-sale.png') no-repeat;
              background-size: 100% 100%;
              vertical-align: middle;
              margin: 0 .533vw;
            }
          }
        }
        .price {
          font-size: 3.2vw;
        }
      }
      .favorite {
        display: block;
        position: absolute;
        bottom: 0;
        right: 16vw;
        font-size: 0;
        text-indent: -9999px;
        width: 5.6vw;
        height: 4.933vw;
        background: url('/_ui/img/m/icn/icn-favorite.png') no-repeat;
        background-size: 100%;
        &:active {
          opacity: 0.23;
        }
      }
      .favorite.on {
        background: url('/_ui/img/m/icn/icn-favorite-on.png') no-repeat;
        background-size: 100%;
      }
      .icn-share {
        position: absolute;
        width: 4.4vw;
        height: 4.933vw;
        right: 6vw;
        bottom: 0;
        background: url('/_ui/img/m/icn/icn-share02.png') no-repeat center center;
        background-size: contain;
        text-indent: -9999px;
        &:active {
          opacity: 0.23;
        }
      }
    }
    .option-box {
      padding: 9.6vw 6.133vw 7.6vw;
      dl {
        overflow: hidden;
        dt {
          float: left;
          clear: both;
          color: #666;
          font-size: 3.466vw;
          width: 26.666vw;
          padding-top: 2.266vw;
        }
        dd {
          float: left;
          width: 61.066vw;
          font-size: 0;
          letter-spacing: 0;
          word-spacing: 0;
          a {
            display: inline-block;
            min-width: 7.733vw;
            height: 7.733vw;
            margin-left: 1.6vw;
            margin-bottom: 1.6vw;
            padding: 0 .66vw;
            box-sizing: border-box;
            img {
              width: 100%;
            }
          }
        }
        .color {
          margin-left: -1.6vw;
          padding-bottom: 4.8vw;
          a {
            position: relative;
            width: 7.733vw;
            height: 7.733vw;
            padding: 0;
            overflow: hidden;
            border: 1px solid rgba(0, 0, 0, 0.1);
          }
          .color-x {
            img {
              opacity: .4;
            }
            &::before {
              content: '';
              display: block;
              background: #e5e5e5;
              width: 10.666vw;
              height: .266vw;
              position: absolute;
              top: 3.6vw;
              left: -1.733vw;
              -webkit-transform: rotate(-45deg);
              -ms-transform: rotate(-45deg);
              transform: rotate(-45deg);
            }
          }
          .on::after {
            content: '';
            display: block;
            width: 7.466vw;
            height: 7.466vw;
            position: absolute;
            top: 0;
            left: 0;
            background: url('/_ui/img/m/icn/icn-check11.png') no-repeat center;
            background-size: 100%;
          }
        }
        .size {
          margin-left: -1.6vw;
          padding-bottom: 2.933vw;
          a {
            box-sizing: border-box;
            border: 1px #000 solid;
            line-height: 7.2vw;
            text-align: center;
            color: #1f1f1f;
            font-size: 3.2vw;
            font-family: 'helvetica';
          }
          .on {
            background: #1f1f1f;
            border: 1px #1f1f1f solid;
            color: #fff;
          }
          .size-x {
            border: 1px #e5e5e5 solid;
            color: #e5e5e5;
            position: relative;
            background: url('/_ui/img/m/icn/bg-slash.png') no-repeat;
            background-size: 100% 100%;
            // &::before {
            //   content: '';
            //   display: block;
            //   background: #e5e5e5;
            //   width: 10.666vw;
            //   height: .266vw;
            //   position: absolute;
            //   top: 3.6vw;
            //   left: -1.733vw;
            //   -webkit-transform: rotate(-45deg);
            //   -moz-transform: rotate(-45deg);
            //   -o-transform: rotate(-45deg);
            //   -ms-transform: rotate(-45deg);
            //   transform: rotate(-45deg);
            // }
          }
        }
        .ea {
          height: 8vw;
          line-height: 8vw;
          padding-top: 0;
          span {
            font-size: 4vw;
          }
          .bts-ctrl {
            float: right;
            width: 22.4vw;
            font-size: 0;
            letter-spacing: 0;
            word-spacing: 0;
            a {
              display: inline-block;
              font-size: 0;
              text-indent: -9999px;
              width: 3.733vw;
              height: 3.733vw;
              margin-bottom: 0;
              vertical-align: middle;
            }
            .plus {
              background: url('/_ui/img/m/icn/icn-plus2.png') no-repeat;
              background-size: contain;
            }
            .minus {
              background: url('/_ui/img/m/icn/icn-minus2.png') no-repeat center;
              background-size: 3.466vw .533vw;
              margin-left: 0.8vw;
            }
          }
        }
      }
    }
    .prod-set {
      border-bottom: 2px solid #f1f1f1;
      img {
        float: left;
        width: 18.133vw;
        height: auto;
        margin-right: 8.266vw;
      }
      .info {
        margin-bottom: 8.266vw;
        .tit {
          margin-bottom: 1.6vw;
          font-size: 3.733vw;
          font-weight: 700;
          color: #1f1f1f;
        }
        .code {
          margin-bottom: 3.2vw;
          font-size: 3.2vw;
          color: #898989;
        }
        .price {
          font-size: 3.2vw;
          font-weight: 600;
          color: #1f1f1f;
        }
      }
      .info + dl {
        clear: both;
      }
    }
    .etc-box {
      background: #f6f6f6;
      dl + div + dl {
        padding: 6.266vw 6.133vw 9.066vw;
      }
      dl {
        padding: 9.066vw 6.133vw 0;
        overflow: hidden;
        font-size: 3.466vw;
        dt {
          float: left;
          clear: both;
          color: #666;
          width: 26.666vw;
          margin-top: 6.266vw;
          &:first-child {
            margin-top: 0;
          }
        }
        dd {
          float: left;
          position: relative;
          width: 61.066vw;
          margin-top: 6.266vw;
          color: #1f1f1f;
          span {
            display: block;
            width: 75%;
            min-height: 4.1333vw;
            word-break: break-all;
          }
          .bt-notice {
            position: absolute;
            top: -.66666vw;
            right: 0;
            padding: 1.333vw;
            font-size: 3.2vw;
            color: #898989;
            border: 1px #e9e9e9 solid;
          }
        }
        dt:first-child + dd {
          margin-top: 0;
        }
        .d-check {
          .tab-bts {
            font-size: 0;
            letter-spacing: 0;
            word-spacing: 0;
            a {
              display: inline-block;
              padding-right: 3.733vw;
              background: url('/_ui/img/m/icn/icn-check03-off.png') no-repeat right center;
              background-size: 2.933vw 2.133vw;
              color: #cbcbcb;
              font-size: 3.466vw;
              margin-left: 4vw;
              &:first-child {
                margin-left: 0;
              }
            }
            .on {
              background: url('/_ui/img/m/icn/icn-check03.png') no-repeat right center;
              background-size: 2.933vw 2.133vw;
              color: #1f1f1f;
            }
          }
        }
      }
      .tab-conts {
        background: #efefef;
        padding: 0 6.133vw;
        .bnr-psent {
          padding-top: 6vw;

          img {
            width: 100%;
          }
        }
        select {
          background: #efefef url('/_ui/img/m/icn/icn-arrow02.png') no-repeat 99% 50%;
          background-size: 3vw;
          height: 10.666vw;
          line-height: 10.666vw;
          border-bottom: 1px #e1e1e1 solid;
          font-size: 3.733vw;
          color: #898989;
        }
        select + select {
          margin-top: 2.666vw;
        }
        .store-select {
          overflow: hidden;
          padding-top: 3.466vw;
          select {
            float: left;
            width: 40vw;
          }
          select + select {
            margin: 0 0 0 5.6vw;
          }
        }
        .store-info {
          font-size: 3.466vw;
          color: #898989;
          padding: 5.6vw 0 8.266vw;
          .t1 {
            font-size: 3.733vw;
            color: #1f1f1f;
            font-weight: 700;
          }
          .t2 {
            margin: 1.066vw 0 2.933vw;
            line-height: 4.8vw;
          }
          .t3 {
            overflow: hidden;
            span {
              font-size: 3.466vw;
              color: #898989;
              float: left;
              width: 50%;
            }
          }
        }
        .store-date + ul {
          padding: 4vw 0 9.6vw;
          color: #aaa;
          font-size: 3.466vw;
          line-height: 4.8vw;
          li {
            margin-top: 1.733vw;
            &:first-child {
              margin-top: 0;
            }
          }
        }
        .disc {
          padding: 6.266vw 0 6.266vw 3.733vw;
          li {
            list-style: disc;
            color: #898989;
            font-size: 3.466vw;
            line-height: 6.133vw;
          }
        }
      }
      .tab-conts > div {
        display: none;
        margin-top: 6.133vw;
      }
      .coupon {
        padding: 0 6.133vw 6.133vw;
        .coupon-list {
          overflow: hidden;
          position: relative;
          margin-top: 2.666vw;
          background: #fff;
          padding: 4.8vw;
          &:first-child {
            margin-top: 0;
          }
        }
        .txt-area {
          position: relative;
          .title {
            width: 85%;
            margin-bottom: 3.33vw;
            font-size: 3.733vw;
            color: #1f1f1f;
          }
          span {
            font-size: 4.266vw;
            color: #1f1f1f;
            font-weight: 700;
          }
          .date {
            font-weight: 300;
            font-size: 3.2vw;
            color: #aaa;
            margin-left: 2.266vw;
          }
        }
        .bt-down {
          position: absolute;
          display: block;
          width: 9.866vw;
          height: 10.4vw;
          top: 50%;
          right: 4.8vw;
          background: #f23d4e url('/_ui/img/m/icn/icn-downlode2.png') no-repeat center;
          background-size: 4.133vw 4.266vw;
          font-size: 0;
          text-indent: -9999px;
          transform: translateY(-50%);
        }
      }
    }
  }
  .favorite-pop {
    display: none;
    position: fixed;
    left: 0;
    right: 0;
    margin: 0 auto;
    top: 49.866vw;
    width: 50.666vw;
    height: 50.666vw;
    z-index: $mini-pop;
    background: url('/_ui/img/m/temp/bg-favorite-pop.png') no-repeat;
    background-size: contain;
    border-radius: 50%;
    i {
      display: block;
      width: 6.666vw;
      height: 5.866vw;
      margin: 16vw auto 3.6vw;
      background: url('/_ui/img/m/icn/icn-favorite.png') no-repeat;
      background-size: 100%;
      opacity: 0;
      animation: fade-in 0.2s linear 0.35s forwards;
    }
    p {
      font-size: 3.733vw;
      color: #1f1f1f;
      text-align: center;
      line-height: 4.8vw;
      display: none;
      opacity: 0;
      animation: fade-in 0.2s linear 0.35s forwards;
    }
    .txt-on {
      display: block;
    }
    &.on {
      i {
        background: url('/_ui/img/m/icn/icn-favorite-on.png') no-repeat;
        background-size: 100%;
        animation: slide-fade-in 0.3s linear 0.3s forwards;
      }
      .txt-on {
        animation: slide-fade-in 0.3s linear 0.5s forwards;
      }
    }
  }
}
@keyframes slide-fade-in {
  0% {
    opacity: 0;
    transform: translateY(3vw);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.product-detail-info {
  position: relative;
  background-color: #fff;
}
.detail-btn-area {
  padding-bottom: 13.6vw;
  overflow: hidden;
  background-color: #fff;
  .btn-area-wrapper {
    height: 11.2vw;
  }
  .btn-area {
    overflow: hidden;
    border-top: .266vw solid rgba(0, 0, 0, 0.05);
  }
  p {
    margin: 7.6vw 0 7.6vw;
    font-size: 3.733vw;
    color: #898989;
    text-align: center;
    strong {
      color: #f23d4e;
      text-transform: uppercase;
    }
  }
  .sold-out {
    margin: 0;
    strong {
      display: block;
      margin-bottom: 1.6vw;

    }
  }
  a {
    float: left;
    display: block;
    width: 42.4vw;
    height: 11.2vw;
    text-align: center;
    line-height: 11.2vw;
    font-size: 4vw;
    box-sizing: border-box;
  }
  .type01 {
    background: #1f1f1f;
    border: .266vw solid #1f1f1f;
    color: #fff;

    &.type01-block {
      width: 84.8vw;
    }
  }
  .type02 {
    border-right: .266vw solid rgba(0, 0, 0, 0.05);
    background: #fff;
  }
  .btn-share {
    width: 15.2vw;
    background: #fff url("/_ui/img/m/icn/icn-share02.png") no-repeat 6.133vw;
    background-size: 2.933vw;
    text-indent: -9999px;
  }
  .go-top {
    position: fixed;
    opacity: 0;
    width: 6.666vw;
    height: 5.466vw;
    bottom: 14.6vw;
    right: 3.466vw;
    background: url('/_ui/img/m/icn/icn-top-02.png') no-repeat center center;
    background-size: contain;
    text-indent: -9999px;
    transition: 0.2s ease;
    z-index: -1;
  }
  .go-top.active {
    opacity: 1;
    bottom: 17.6vw;
  }
  &.fixed {
    position: fixed;
    bottom: -11.2vw;
    width: 100%;
    z-index: $icn-navi-zindex;
    transition: 0.1s ease;
    padding-bottom: 0;
  }
  &.show {
    bottom: 0;
  }
}
.area-banner {
  margin: 0 -6.133vw;
  a {
    display: block;
    font-size: 0;
    margin-bottom: 0.533vw;
  }
}
.product-notice {
  padding: 0 7.466vw;
  .h1-top {
    height: 14.666vw;
    line-height: 14.666vw;
    position: relative;
    .pop-close {
      display: block;
      position: absolute;
      top: 5.333vw;
      right: 4.8vw;
      width: 4vw;
      height: 4vw;
      background: url('/_ui/img/m/icn/icn-close.png') no-repeat;
      background-size: 100%;
      font-size: 0;
      text-indent: -9999px;
    }
    h1 {
      color: #1f1f1f;
      font-weight: 700;
      text-align: center;
      font-size: 4.533vw;
    }
  }
  h3 {
    margin: 5.333vw 0 4vw;
    font-size: 3.733vw;
    color: #1f1f1f;
    font-weight: 700;
  }
  .h1-top + h3 {
    margin: 6.4vw 0 3.733vw;
  }
  .box {
    border-top: 2px #000 solid;
    border-bottom: 2px #000 solid;
    padding: 1.866vw 0;
    .card-non {
      font-size: 3.733vw;
      padding: 17.066vw 0;
      text-align: center;
      color: #aaa;
    }
    dl {
      overflow: hidden;
      dt {
        float: left;
        clear: both;
        width: 25.866vw;
        border-top: 1px #f1f1f1 solid;
        text-align: center;
        img {
          display: inline-block;
          vertical-align: middle;
          height: 6.133vw;
          padding-top: 3.6vw;
        }
        &:first-child {
          border-top: 0;
        }
      }
      dd {
        float: left;
        width: 64.533vw;
        border-top: 1px #f1f1f1 solid;
      }
      dt:first-child + dd {
        border-top: 0;
      }
    }
    .card1 {
      dt {
        width: 20vw;
        // height: 17.6vw;
        line-height: 18.666vw;
      }
      dd {
        width: 59.2vw;
        padding: 3.6vw 0 3.6vw 5.866vw;
        p {
          font-size: 3.466vw;
          color: #1f1f1f;
          margin-bottom: 1.066vw;
        }
        span {
          font-size: 3.466vw;
          color: #898989;
          letter-spacing: -.133vw;
        }
      }
    }
    .card2 {
      font-size: 3.466vw;
      dt {
        // height: 13.866vw;
        width: 20vw;
        line-height: 13.866vw;
        color: #898989;
        text-align: left;
      }
      dd {
        width: 59.2vw;
        // height: 13.866vw;
        padding-left: 5.866vw;
        line-height: 13.866vw;
        color: #1f1f1f;
        img {
          display: inline-block;
        }
      }
    }
  }
  .box + h3 {
    margin: 12vw 0 4vw;
  }
  .box2 {
    font-size: 3.733vw;
    .t1 {
      color: #1f1f1f;
    }
    .t2 {
      color: #898989;
    }
    .dl-list {
      border-top: 2px #1f1f1f solid;
      margin-top: 6.266vw;
      dt {
        color: #1f1f1f;
        padding-top: 5.2vw;
      }
      dd {
        color: #898989;
        border-bottom: 1px #f1f1f1 solid;
        padding-bottom: 4.933vw;
        margin-top: 1.6vw;
        li {
          margin-top: 3.466vw;
          p {
            color: #666;
            margin-bottom: 1.2vw;
          }
          span {
            color: #aaa;
          }
        }
        em {
          color: #1f1f1f;
          display: block;
          margin-top: 1.466vw;
        }
        &:last-child {
          border-bottom: 0;
          padding-bottom: 0;
        }
      }
      .unite-notice {
        dt {
          padding-bottom: 1.066vw;
          font-weight: 700;
          &:first-child {
            font-weight: normal;
            padding-bottom: 0;
          }
        }
        dd {
          border: none;
        }
      }
    }
    .dl-table {
      border-top: 2px #000 solid;
      border-bottom: 2px #000 solid;
      padding: 2.8vw 0;
      overflow: hidden;
      line-height: 5.733vw;
      font-size: 3.466vw;
      dt {
        float: left;
        width: 45.066vw;
        padding: 1.866vw 0;
        color: #898989;
        border-top: 1px #f1f1f1 solid;
        &:first-child {
          border-top: 0;
        }
      }
      dt:first-child + dd,
      dt:first-child + dd + dd {
        border-top: 0;
      }
      dd {
        float: left;
        width: 20vw;
        padding: 1.866vw 0;
        color: #1f1f1f;
        border-top: 1px #f1f1f1 solid;
        letter-spacing: -1px;
      }
